<template>
  <b-container fluid>
    <b-row>
      <b-col v-for="(product, idx) in products" :key="idx" lg="2" md="3" sm="4" cols="6" class="my-2">
        <product-box :product="product"></product-box>
      </b-col>
    </b-row>
    <b-container fluid class="d-flex justify-content-center">
      <b-overlay :show="busy" opacity="0.4" spinner-small
                        spinner-variant="primary" class="d-inline block">
        <b-button variant="success" :disabled="busy" v-if="!productLoadingDone" @click="loadMore">
          Load More
        </b-button>
      </b-overlay>
    </b-container>
  </b-container>
</template>

<script>
import loader from '../../mixins/loader'
import ProductBox from '../../components/Boxes/ProductBox'
export default {
  name: 'OurShop',
  data () {
    return {
      products: [],
      productLoadingDone: true,
      busy: false
    }
  },
  mixins: [
    loader
  ],
  components: {
    ProductBox
  },
  methods: {
    loadMore () {
      if (!this.productLoadingDone) this.busy = true
      const offset = this.products.length
      const limit = process.env.VUE_APP_PAGE_LIMIT
      this.loader('products/our-shop', { offset, limit }, 'our-shop')
        .then(response => {
          this.busy = false
          this.products = this.products.concat(response.data.products)
          if (response.data.products.length < limit) {
            this.productLoadingDone = true
          } else {
            this.productLoadingDone = false
          }
        })
    }
  },
  created () {
    this.loadMore()
  }
}
</script>

