<template>
  <div>
    <div id="holder" :class="{ shadow }" @mouseenter="shadow = true" @mouseleave="shadow = false">
      <b-overlay :show="busy" opacity="0.7" spinner-large spinner-variant="primary"  @hidden="onHidden">
        <div @click="clicking">
<!--          <b-card :img-src="imgSrc" img-height="200px" :img-alt="product.name" img-top style="border-radius: 0;">-->
          <b-card style="border-radius: 0;">
            <b-card-img ref="img" :src="imgSrc" :alt="product.name" :height="isArrival? '100px':'200px'" top style="border-radius: 0;">
            </b-card-img>
            <!-- <b-card-title style="font-size: 110%;">
              {{product.name}}
            </b-card-title> -->
            <b-card-text style="padding: 0 5%;">
              <div class="text-truncate">
                {{product.name}}
              </div>
              <b-form-rating
              v-if="!isArrival"
                inline
                no-border
                variant="warning"
                readonly
                size="sm"
                v-model="product.rating"
                precision="1"
                style="transform: scale(0.8, 0.8);"
              >
              </b-form-rating>
              <div v-if="!isArrival">
                <span v-if="product.discount > 0">
                  <span id="prev-price" class="strike">&#2547;{{prevPrice}}</span>
                  <sup id="discount">-{{product.discount}}%</sup>
                </span>
                <span id="cur-price">&nbsp;&nbsp;&#2547;{{product.curPrice}}</span>
              </div>
            </b-card-text>
          </b-card>
        </div>
      </b-overlay>
      <b-button
        id="add-to-cart"
        :aria-controls="'sidebar-right-footer'+product.id"
        :aria-expanded="show"
        @click="initialHandler(true)"
        squared
        v-if="!isArrival"
        class="mx-auto my-auto text-center"
        :style="{ opacity: shadow * 1, border: 0 }"
      >
        Add to Bag
      </b-button>
      <!-- <b-button
        id="buy-now"
        :aria-controls="'sidebar-right-footer-buy'+product.id"
        aria-expanded=""
        @click="initialHandler(false)"
        squared
        :style="{ opacity: shadow * 1, border: 0 }"
      >
        Buy Now
      </b-button> -->
    </div>
    <b-sidebar
      :id="'sidebar-right-footer'+product.id"
      aria-label="Sidebar with custom footer"
      v-model="show"
      shadow
      backdrop
      lazy>
      <template v-slot:title>
        <span class="">There are some options.<br>Choose yours:</span>
      </template>
      <template v-slot:footer>
        <div class="mx-auto text-center">
          <b-button squared variant="warning" @click="doReset" class="col-6">Reset</b-button>
          <b-button @click="finalHandler" squared variant="success" class="col-6" :disabled="!allSelected">
            {{cartButtonPressed ? 'Add to Bag' : 'Check Out'}}
          </b-button>
        </div>
      </template>
      <b-overlay :show="!productLoaded" opacity="0.4" rounded="sm" style="height:500px">
        <template v-slot:overlay>
          <div class="text-center">
            <b-icon icon="stopwatch" font-scale="3" animation="cylon"></b-icon>
            <p id="cancel-label">Please wait...</p>
          </div>
        </template>
        <div class="mx-auto d-flex flex-column text-center" v-if="productLoaded">
          <b-list-group>
            <span
              v-for="(values, key, index) in productFull.specifications"
              :key="index">
              <b-list-group-item v-if="Array.isArray(values)">
                <b-form-group :label="key">
                <b-form-select style="width: 80%"
                               v-model="selector[key]" :options="values" :value-field="key">
                  <template v-slot:first>
                    <b-form-select-option :value="null" disabled>-- Please select an option --</b-form-select-option>
                  </template>
                </b-form-select></b-form-group>
              </b-list-group-item>
            </span>
            <b-list-group-item>
              <label :for="'sb-inline' + product.id">Quantity</label>
              <b-form-spinbutton class="ml-2" :id="'sb-inline' + product.id" v-model="quantity" inline></b-form-spinbutton>
            </b-list-group-item>
          </b-list-group>
        </div>
      </b-overlay>
    </b-sidebar>
    <!-- <span v-for="([key, values], index) in Object.entries(productFull.specifications)" :key="index">
    <b-form-group :label="'Choose '+key" v-if="Array.isArray(values)">
      <b-form-checkbox-group
        v-model="selector[key]"
        :options="values"
        size="sm"
        stacked
      >
      </b-form-checkbox-group>
    </b-form-group></span> -->
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import loader from '../../mixins/loader'

export default {
  props: {
    product: {
    },
    isArrival: {
      default: false
    }
  },
  data () {
    return {
      busy: false,
      shadow: false,
      productFull: {},
      productLoaded: false,
      selector: {},
      show: false,
      cartButtonPressed: null,
      quantity: 1,
      imgWidth: 0
    }
  },
  computed: {
    ...mapGetters([
      'authToken'
    ]),
    allSelected () {
      if (!this.productLoaded) {
        return false
      }
      for (const key in this.selector) {
        // eslint-disable-next-line no-prototype-builtins
        if (this.selector.hasOwnProperty(key)) {
          if (Array.isArray(this.productFull.specifications[key]) && this.selector[key] === null) {
            return false
          }
        }
      }
      return true
    },
    imgSrc () {
      return this.product.image ? this.product.image.link : ''
    },
    prevPrice () {
      return Math.round(this.product.curPrice / (1 - this.product.discount / 100))
    }
  },
  mixins: [
    loader
  ],
  methods: {
    ...mapActions([
      'addToCart'
    ]),
    loadProduct () {
      if (this.productLoaded) {
        return
      }
      const id = this.product.id
      this.loader('/product', { productID: id }, `specs of product-${id}`)
        .then(response => {
          this.productFull = response.data
          this.productFull.specifications = this.productFull.extra
          delete this.productFull.extra
          this.productLoaded = true
          this.loadSelector()
        })
    },
    loadSelector () {
      this.selector = JSON.parse(JSON.stringify(this.productFull.specifications)) // deep(disconnected) copy
      for (const key in this.selector) {
        // eslint-disable-next-line no-prototype-builtins
        if (this.selector.hasOwnProperty(key)) {
          if (Array.isArray(this.productFull.specifications[key])) {
            this.$set(this.selector, key, null)
          }
        }
      }
      console.log('done')
    },
    makeOrder () {
      const curPrice = this.productFull.price.sellingPrice
      return {
        products: [{
          id: this.productFull.productID,
          name: this.productFull.name,
          specifications: this.selector,
          quantity: this.quantity,
          image: this.productFull.images[0],
          curPrice,
          discount: this.productFull.price.discount,
          vat: this.productFull.price.vat
        }],
        subTotal: curPrice * (1 + this.productFull.price.vat / 100) * this.quantity,
        deliveryCharge: 60
      }
    },
    initialHandler (cartButtonPressed) {
      if (!this.authToken && !cartButtonPressed) {
        this.$bvModal.show('login-modal')
      } else {
        this.loadProduct()
        this.show = true
        this.cartButtonPressed = cartButtonPressed
      }
    },
    finalHandler () {
      if (!this.allSelected) {
        return
      }
      if (this.cartButtonPressed) {
        this.addToCart([{
          productID: this.productFull.productID,
          specifications: this.selector,
          quantity: this.quantity,
          curPrice: this.product.curPrice,
          discount: this.product.discount,
          image: this.product.image,
          name: this.product.name,
          vat: this.productFull.price.vat
        }]).then().catch(error => {
          console.log(error.response)
        })
      } else {
        this.$router.push({
          name: 'OrderSubmitDetails',
          query: {
            order: this.makeOrder()
          }
        })
      }
      this.show = false
    },
    doReset () {
      for (const key in this.selector) {
        // eslint-disable-next-line no-prototype-builtins
        if (this.selector.hasOwnProperty(key)) {
          if (Array.isArray(this.productFull.specifications[key])) {
            this.$set(this.selector, key, null)
          }
        }
      }
    },
    onHidden () {
      this.$refs.button.focus()
    },
    clicking () {
      this.busy = true
      this.$router.push({ name: 'CertainProduct', params: { id: this.product.id } })
    }
  },
  mounted () {
    this.imgWidth = this.$refs.img.clientWidth
  }
}
</script>

<style scoped>
#holder {
  cursor: pointer;
  padding: 0;
  text-align: center;
}
.card-body { /* this is needed */
  padding: 0;
}
#prev-price {
  font-size: 90%;
  color: grey;
}
#discount {
  font-size: 70%;
  color: red;
}
#cur-price {
  font-size: 110%;
  color: darkorange;
}
